@use "ag-grid-community/styles" as ag;
@import "../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";

@include ag.grid-styles((
        theme: quartz,
        --ag-background-color: var(--background-color),
        --ag-foreground-color: var(--text-color),
        --ag-text-color: var(--ag-foreground-color),
        --ag-header-background-color: var(--background-table-head),
        --ag-header-cell-moving-background-color: var(--background-table-head),
        --ag-border-color: grey,
        --ag-row-border: solid 1px var(--ag-border-color),
));


.ag-theme-quartz {
  height: 90vh;
  //display: flex;
  //justify-content: center;
  //align-items: center;
  //width: 100%;

  margin-top: 40px;

  @include respond-to(628px) {
    margin-top: 20px;
  }

  & .ag-table {
    @include respond-to(628px) {
      display: none;
    }
  }


  .filter-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }


  & .ag-body-vertical-scroll {
    display: none;
  }

  & .ag-picker-collapsed, & .ag-picker-field-wrapper {
    background: var(--background-color);
  }

  & .ag-checkbox-input::after {
   color: #4e4e4e;

  }

  //& .ag-header-container {
  //  width: 100% !important;
  //  .ag-header-row {
  //    width: 100% !important;
  //
  //  }
  //
  //
  //}

  //& .ag-header-cell {
  //  width: 400px !important;
  //}

  & .ag-header-cell-text {
    color: #fff;
  }

  & .ag-header-cell-resize {
    background: #fff;
    width: 2px;
  }

  & .ag-header-cell-resize::after {
    background: none;
  }

  & .ag-column-moving {
    width: 100px !important;
  }


  & .ag-header-container {
    width: 100%;
    //justify-content: space-between;
    //display: flex;
  }

  & .ag-header-cell-sortable {
    height: 40px !important;
  }

  //& .ag-header-viewport {
  //  background: red;
  //  border-radius: 9px;
  //  height: 40px;
  //
  //}

  & .ag-root-wrapper {
    border: none;
    background: none;
  }

  & .ag-root {
    padding: 20px;

    @include respond-to(628px) {
      margin-top: 20px;
    }
    ///*width: 90%;
  }


  & .ag-body {
    //z-index: 1;
    box-shadow: 0 4px 8px 1px rgba(0, 0, 0, 0.2);
    background: var(--background-table-container);
    //width: 90%;
  }


  & .ag-header-cell-text {
    font-family: $mts-wide-b;
    font-size: 16px;
  }

  & .ag-cell-value {
    line-height: 41px;
    font-family: $mts-text-r;
    font-size: 16px;
  }

  & .ag-row-even, .ag-row-odd {
    background: var(--background-table-container);
    border-bottom: 1px solid #bebbbb;

  }

  & .ag-checkbox-input-wrapper {
    background: #fff;
  }

  & .ag-header {
    margin-bottom: 30px;
    border: none;
    // background: var(--background-table-head);
    height: 40px !important;
    min-height: 40px !important;
    border-radius: 9px;
    //width: 90%;
  }

  //& .ag-pinned-left-header {
  //  background: #ff0032;
  //}

  & .ag-body-horizontal-scroll {
    //TODO:
    display: none;
  }

  & .ag-popup {
    height: 0px;
    margin: 0;
    padding: 0;
  }
}
