@import "../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";


.error_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 90vh;
  color: var(--text-color);

  @include respond-to(458px) {
    padding: 10px;
  }
  h2 {
    font-family: $mts-wide-b;
    font-size: 35px;

    @include respond-to(1078px) {
      font-size: 28px;
    }
    @include respond-to(650px) {
      font-size: 20px;
    }
    //@include respond-to(458px) {
    //  font-size: 18px;
    //}
  }

  h1 {
    font-family: $mts-wide-b;
    font-size: 100px;
    font-weight: 400;
    @include respond-to(1078px) {
      font-size: 70px;
    }
    @include respond-to(650px) {
      font-size: 50px;
    }
    @include respond-to(458px) {
      font-size: 35px;
    }

  }

  p {
    display: inline-block;
    width: 50%;
    text-align: center;
    font-family: $mts-text-r;
    font-size: 20px;
    @include respond-to(650px) {
      font-size: 18px;
    }

    @include respond-to(458px) {
      font-size: 16px;
      width: 100%;
    }
  }

  .form_box {

    display: flex;
    flex-direction: row;
    width: 50%;
    gap: 30px;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;

    @include respond-to(820px) {
      flex-direction: column;
    }

    @include respond-to(458px) {
      width: 90%;
    }


    .input_field {
      width: 70%;
      height: 50px;
      padding: 10px;
      border-radius: 12px;
      outline: none;
      border: 1px solid var(--border-color);
      background-color: var(--background-color);
      @include respond-to(820px) {
        width: 100%;
        height: 80px;
      }

    }

    .button {
      width: 200px;
      height: 50px;
      text-align: center;
      background-color: var(--color-button);
      border-radius: 18px;
      font-family: $mts-wide-b;
      font-size: 16px;
      color: #fff;

      @include respond-to(650px) {
        width: 100%;
      }


      &:hover {
        background-color: var(--button-hover);
      }
    }
  }

}