@import "../../../../style/global.scss";
@import "src/style/screen-size.scss";

.filter_wrapper {
  display: flex;
  max-width: 80%;
  max-height: 50px;
  align-items: center;
  margin: 0 auto;
  justify-content: space-between;
  overflow: hidden;
  gap: 12px;
  background-color: var(--background-color);
  padding: 10px;
  border-radius: 12px;
  box-shadow: $shadow-high;

  @include respond-to(1370px) {
    max-height: 45px;
  }

  @include respond-to(lg) {
    justify-content: space-around;
    padding: 12px;
  }


  .icons_container {
    display: flex;
    align-items: center;
    gap: 10px;
  }


  @include respond-to(lg) {
    padding: 5px;
  }


  .list_conteiner {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: start;
    max-width: 90%;
    padding-top: 10px;
    max-height: 50px;
    overflow: hidden;
    gap: 12px;

    @include respond-to(xl) {
      padding-top: 15px;
    }

    @include respond-to(1370px) {
     // display: none;
    }
  }

  .svg_icon_filter {
    cursor: pointer;
    width: 28px;
    height: 28px;
    padding: 5px;
    border-radius: 8px;
    box-shadow: $filters2-shadow;
    path {
      fill: var(--text-color);
    }

    @include respond-to(lg) {
      width: 25px;
      height: 25px;
    }
  }

  .svg_icon {
    path {
      fill: var(--text-color);
    }
  }




}

.filter,
.filter_active {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 3px 8px 3px 8px;
  gap: 12px;
  border-radius: 8px;
  background-color: var(--background-color-secondary);
  box-shadow: $filters2-shadow;
  cursor: pointer;

  &:last-child {
    @include respond-to(1370px) {
      display: none;
    }
  }


  p {
    font-family: $mts-text-r;
    font-size: 16px;
    font-weight: 400;

    @include respond-to(xl) {
      font-size: 12px;
    }

    @include respond-to(1370px) {
      font-size: 10px;
    }


  }


  .filter_check {
    path {
      stroke: var(--text-color);
    }
  }

  .filter_check_active {
    width: 17px;
    height: 16px;
    path {
      fill: var(--text-color);
    }
  }
}

.filter_active {
  background: var(--gradient);

  p {
    color: #fff;
  }

  .filter_check_active {
    path {
      fill: #fff;
    }
  }
}


.filter_wrapper_expanded {
  @extend .filter_wrapper;
  align-items: start;
  max-height: 250px;
  transition: width 0.7s ease;

  .list_container_expanded {
    @extend .list_conteiner;
    padding-top: 0;
    max-height: 350px;
  }
}