@import "../../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";


.table_mobile {
  display: none;
  flex-direction: column;
  padding: 20px;

  @include respond-to(628px) {
    display: flex;
  }


  .selected_container {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    p {
      font-family: $mts-wide-b;
      font-size: 14px;
      color: var(--text-color);
    }

    button {
      background-color: var(--color-button);
      border-radius: 8px;
      padding: 10px;
      font-family: $mts-wide-m;
      font-size: 14px;
      color: #fff;
    }

    .svg_icon {
      path {
        fill: var(--text-color);
      }
    }
  }

  .not_selected {
    display: none;
  }

  .list_widgets {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
}