@import "src/style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";

.docs_container {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 24px 28px;
  border-radius: 24px;
  border: 1px solid $border-color;
  flex: 1 0 0;
  max-width: 450px;
  gap: 10px;

  h1 {
    font-family: $mts-wide-b;
    font-size: 16px;
    color: var(--text-color);
    @include respond-to(xs) {
      font-size: 14px;
    }
  }

  input {
    width: 100%;
    background-color: var(--background-color-secondary);
    outline: none;
    border-radius: 18px;
    padding: 10px;
    border: none;
    box-shadow: $shadow-middle;
  }

  @include respond-to(lg) {
    max-width: 400px;
  }

  @include respond-to(650px) {
    padding: 14px 18px;
    max-width: 370px;
  }

  @include respond-to(440px) {
    max-width: 320px;
  }

  @include respond-to(350px) {
    padding: 10px 14px;
    max-width: 300px;
  }

  .title {
    position: absolute;
    left: 26px;
    top: -17px;
    font-family: $mts-wide-b;
    font-size: 20px;
    background-color: var(--background-notif-window);
    color: var(--text-color);
    padding: 0 4px;

    @include respond-to(lg) {
      left: 26px;
      top: -18px;
      font-size: 18px;
    }

    @include respond-to(440px) {
      font-size: 16px;
      left: 22px;
      top: -18px;
      max-height: 23px;
      min-width: max-content;
    }
    @include respond-to(330px) {
      font-size: 14px;
    }
  }



  .data_container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    div {
      display: flex;
      flex-direction: column;
      gap: 5px;
      p {
        font-family: $mts-wide-b;
        font-size: 16px;
        @include respond-to(xs) {
          font-size: 14px;
        }
        @include respond-to(350px) {
          font-size: 12px;
        }
      }
    }
  }
}
