@import "src/style/global.scss";
@import "src/style/screen-size.scss";

.confirmWindow {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  background-color: var(--background-color-secondary);
  box-shadow: $shadow-middle;
  border-radius: 24px;
  width: 402px;
  height: fit-content;
  padding: 28px 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  h1 {
    color: var(--text-color);
    font-family: $mts-wide-b;
    font-size: 20px;
  }

  &_btnsContainer {
    display: flex;
    flex-direction: row;
    justify-content: center;

    &_btn {
      width: 90px;
      height: 63px;
    }
  }
}
