@import "../../../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";

.table_widget {
  width: 100%;
  max-height: 260px;
  border-radius: 18px;
  box-shadow: $widget-shadow;
  overflow: hidden;
  background-color: var(--background-color);

  h1 {
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: $mts-wide-m;
    font-size: 16px;
  }

  .row_widget {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    list-style: none;
    overflow: hidden;
    height: 150px;

    li {
      width: 100%;
      padding: 15px;
      border-bottom: var(--border-table-rows);
      font-family: $mts-text-r;
      font-size: 14px;

      &:first-child {
        border-top: var(--border-table-rows);
      }
    }
  }

  .button {
    width: 100%;
    border-radius: 0 0 18px 18px;
    padding: 15px;
    font-family: $mts-wide-b;
    font-size: 16px;
    background-color: var(--color-button);
    color: #fff;
  }

}

.table_widget_selected {
    @extend .table_widget;
    border: 3px solid var(--color-button);

  & .button_selected {
    @extend .button;
    border: 1px solid var(--color-button);
  }

}
