@import "../../../style/global.scss";
@import "src/style/screen-size.scss";

.filter_container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 20px;



    @include respond-to(628px) {
        display: none;
    }

    .title_container h1 {
        font-family: $mts-wide-b;
        font-size: 24px;
        @include respond-to(1370px) {
            font-size: 20px;
        }

        @include respond-to(lg) {
            font-size: 16px;
        }
    }

    .button {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 255px;
        justify-content: space-around;
        background-color: var(--color-button);
        border-radius: 8px;
        padding: 7px 10px;

        @include respond-to(1370px) {
            width: 230px;
        }

        //@include respond-to(lg) {
        //    width: 200px;
        //}

        p {
            color: #fff;
            font-family: $mts-wide-b;
            font-size: 20px;
            @include respond-to(xl) {
                font-size: 16px;
            }

            @include respond-to(1370px) {
                font-size: 14px;
            }


            @include respond-to(lg) {
                font-size: 12px;
            }

        }
        .svg_icons {
            width: 21px;
            height: 23px;
            path {
                fill: #fff;
            }
            @include respond-to(xl) {
                width: 18px;
                height: 20px;
            }

            @include respond-to(1370px) {
                width: 16px;
                height: 14px;
            }

            @include respond-to(lg) {
                width: 14px;
                height: 12px;
            }
        }
    }
   
}