@import "../../../../style/global.scss";

.modal_active {
    display: block;
    position: absolute;
    right: calc(32% - 75px);
    top: 187px;
    z-index: 10;
    .wrapper {
        background-color: var(--background-color);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 24px 24px 0 0;
        padding: 20px;
        gap: 10px;
        .title {
            gap: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            p {
                color: var(--text-color);
                font-family: $mts-wide-m;
                font-size: 18px;
            }
            .svg_icon {
                cursor: pointer;
                path {
                    fill: var(--text-color)
                }
            }
        }
    
        .list_conteiner {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            gap: 10px;
        }
    }
    
}

.modal_off {
    display: none;
}


.new_filter {
    display: flex;
    align-items: center;
    justify-content: space-around;
    background-color: var(--background-color-secondary);
    border-radius: 8px;
    width: 100%;
    padding: 5px;
    box-shadow: $filters2-shadow;
    p {
        color: var(--text-color);
        font-family: $mts-text-r;
        font-size: 18px;
    }

    .svg_icon {
        path {
            fill: var(--text-color);
        }
    }
}

.button_new_filter {

    height: 50px !important;
    
    p {
        font-size: 18px !important;
        font-family: $mts-wide-m !important;
    }

    img {
        width: 16px !important;
        height: 16px !important;
    }
  
}