@import "../../../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";


.filters_modal {
  display: flex;
  flex-direction: column;
  background-color: var(--background-color);
  border-radius: 24px;

  .title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: var(--border-table-rows);
    padding: 20px;
    width: 100%;

    h1 {
      font-family: $mts-wide-m;
      font-size: 18px;
      color: var(--text-color);
    }

    .svg_icons {
      path {
        fill: var(--text-color);
      }
    }
  }

  & .list_container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 20px;
  }

  .setting_filter {
    border-top: var(--border-table-rows);
    h1 {
      padding: 20px;
      font-family: $mts-text-r;
      font-size: 16px;
    }

   .list_container {
      display: flex;
      flex-direction: column;
      gap: 10px;
      padding: 20px;
    }
  }

  .button_container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 20px;

    button {
      padding: 15px;
      border-radius: 18px;
      background-color: var(--color-button);
      color: #fff;
      font-family: $mts-wide-b;
      font-size: 18px;
      box-shadow: $shadow-middle;

      @include respond-to(520px) {
        font-size: 16px;
      }

      &:first-child {
        background-color: var(--background-color-secondary);
        border: 1px solid var(--color-button);
        color: var(--color-button);
      }
    }
  }
}