@import "src/style/global.scss";
@import "src/style/screen-size.scss";

.services_window {
  position: absolute;
  top: 80px;
  right: 22px;
  background-color: var(--background-color);
  box-shadow: $shadow-middle;
  border-radius: 24px;
  width: 428px;
  height: fit-content;
  padding: 28px 30px 0 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include respond-to(710px) {
    top: 61px;
    right: 50%;
    transform: translate(50%, 0);
    width: 85%;
    gap: 14px;
    border-radius: 18px;
  }

  @include respond-to(675px) {
    gap: 10px;
  }

  @include respond-to(500px) {
    padding: 28px 20px 0 20px;
    width: 90%;
  }

  @include respond-to(xs) {
    gap: 8px;
  }

  h1 {
    color: var(--text-color);

    font-family: $mts-wide-b;
    font-size: 20px;

    @include respond-to(710px) {
      font-size: 19px;
    }

    @include respond-to(xs) {
      font-size: 18px;
    }
  }

  &_container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 24px;

    @include respond-to(710px) {
      justify-content: center;
      gap: 14px;
    }

    @include respond-to(675px) {
      gap: 10px;
    }

    @include respond-to(xs) {
      gap: 8px;
    }

    &_empty {
      p {
        color: var(--text-color);
        font-family: $mts-wide-m;
        font-size: 18px;
      }
    }

    &_service {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      align-content: center;
      background-color: var(--background-color);
      border-radius: 18px;
      width: fit-content;
      cursor: pointer;
      padding: 12px 8px 8px 8px;

      &:hover {
        background-color: var(--background-color-secondary);
        box-shadow: $shadow-high;
      }

      &:hover &_icon {
        svg {
          path {
            fill: var(--fill-icon-service-hover);
          }
        }
      }

      &_icon {
        svg {
          path {
            fill: var(--fill-icon-service);
          }
        }
      }

      &_pin {
        position: absolute;
        top: 5px;
        right: 5px;

        svg {
          path {
            fill: var(--fill-icons);
          }
        }
      }
    }
  }

  &_btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 24px;
    padding: 16px 0;
    border-top: 1px solid $border-color;
    cursor: pointer;

    p {
      font-family: $mts-wide-r;
      font-size: 20px;
      padding-bottom: 4px;

      @include respond-to(710px) {
        font-size: 19px;
      }

      @include respond-to(xs) {
        font-size: 18px;
      }
    }

    svg {
      width: 16px;
      height: 8px;
      transform: rotate(90deg);

      path {
        fill: var(--fill-icons);
      }
    }
  }
}
