@import "src/style/global.scss";
@import "src/style/screen-size.scss";

.allServices {
  position: absolute;
  top: 80px;
  right: 22px;
  background-color: var(--background-color);
  box-shadow: $shadow-middle;
  border-radius: 24px;
  width: 664px;
  height: 92vh;
  padding: 28px 20px 20px 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include respond-to(710px) {
    top: 61px;
    right: 50%;
    transform: translate(50%, 0);
    width: 85%;
    gap: 14px;
    border-radius: 18px;
  }

  @include respond-to(675px) {
    padding: 28px 20px 20px 20px;
    gap: 10px;
  }

  @include respond-to(500px) {
    padding: 16px 10px 10px 10px;
    width: 90%;
  }

  @include respond-to(xs) {
    gap: 8px;
  }

  &_head {
    display: flex;
    flex-direction: row;
    gap: 24px;

    h1 {
      color: var(--text-color);
      font-family: $mts-wide-b;
      font-size: 22px;

      @include respond-to(710px) {
        font-size: 20px;
      }

      @include respond-to(xs) {
        font-size: 18px;
      }
    }

    input {
      display: flex;
      width: 60%;
      height: 39px;
      padding: 2px 18px 3px 18px;
      align-items: center;
      flex-shrink: 0;
      border-radius: 8px;
      border: 1px solid $border-color;
      color: var(--text-color);
      background-color: var(--background-color);
      font-family: $mts-wide-r;
      font-size: 18px;
      outline: none;

      &::placeholder {
        color: var(--text-color);
      }

      @include respond-to(710px) {
        font-size: 17px;
        flex: 1;
        margin-right: 24px;
      }

      @include respond-to(xs) {
        height: 30px;
        font-size: 16px;
      }
    }

    &_cross {
      position: absolute;
      top: 34px;
      right: 14px;
      cursor: pointer;

      @include respond-to(500px) {
        top: 24px;
        right: 5px;
      }

      svg {
        path {
          fill: var(--fill-icon-service);
        }
      }
    }
  }

  &_container {
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-y: scroll;

    &::-webkit-scrollbar {
      width: 10px;
      height: 7px;
    }

    &::-webkit-scrollbar-track {
      background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $border-color;
      border-radius: 5px;
    }

    &_category {
      display: flex;
      flex-direction: column;
      gap: 10px;
      // padding-bottom: 10px;

      @include respond-to(400px) {
        gap: 8px;
      }

      h2 {
        color: var(--text-color);
        font-family: $mts-wide-m;
        font-size: 18px;

        @include respond-to(710px) {
          font-size: 17px;
        }

        @include respond-to(xs) {
          font-size: 16px;
        }
      }

      &_services {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        gap: 16px 24px;

        @include respond-to(710px) {
          justify-content: center;
          gap: 12px 20px;
        }

        @include respond-to(675px) {
          gap: 8px;
        }

        &_service {
          display: flex;
          flex-direction: column;
          position: relative;
          width: 124px;
          gap: 8px;
          padding: 5px;
          border-radius: 18px;
          cursor: pointer;

          @include respond-to(675px) {
            width: 115px;
            gap: 0px;
          }

          @include respond-to(434px) {
            width: 103px;
          }

          @include respond-to(394px) {
            width: 96px;
          }

          &:hover {
            background-color: var(--background-color-secondary);
            box-shadow: $shadow-high;
          }

          &:hover &_title_icon {
            svg {
              path {
                fill: var(--fill-icon-service-hover);
              }
            }
          }

          &:hover &_title_pin {
            display: block;
          }

          &_title {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            gap: 10px;

            @include respond-to(675px) {
              gap: 0px;
            }

            &_pin {
              position: absolute;
              top: 6px;
              right: 8px;

              svg {
                path {
                  fill: var(--fill-icons);
                }
              }

              &_unpinned {
                display: none;
              }

              &_pinned {
                display: block !important;
              }
            }

            p {
              color: var(--text-color);
              font-family: $mts-text-r;
              font-size: 18px;

              @include respond-to(710px) {
                font-size: 17px;
              }

              @include respond-to(xs) {
                font-size: 16px;
              }
            }

            &_icon {
              svg {
                @include respond-to(xs) {
                  width: 40px;
                  height: 40px;
                }
                path {
                  fill: var(--fill-icon-service);
                }
              }
            }
          }

          p {
            padding: 0 3px;
            color: var(--text-color);
            font-family: $mts-text-r;
            font-size: 14px;

            @include respond-to(710px) {
              font-size: 13px;
            }

            @include respond-to(xs) {
              font-size: 12px;
            }
          }
        }
      }
    }
  }
}
