@import "../../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";


.filter_mobile {
  display: none;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 20px;

  @include respond-to(628px) {
    display: flex;
  }

  h1 {
    font-family: $mts-wide-b;
    font-size: 18px;
    @include respond-to(xs) {
      font-size: 14px;
    }
  }

  .filter_wrapper {
    border-radius: 8px;
    border: 1px solid var(--border-color);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    box-shadow: $shadow-low;

    .filter_icon {
      path {
        fill: var(--text-color);
      }
    }
  }

  .button {
    border-radius: 8px;
    display: flex;
    align-items: center;
    padding: 10px;
    gap: 10px;
    background-color: var(--color-button);
    p {
      color: #fff;
      font-family: $mts-wide-b;
      font-size: 16px;
      @include respond-to(xs) {
        font-size: 12px;
      }
    }

    .svg_icon {
      width: 16px;
      height: 8px;
      @include respond-to(xs) {
        width: 12px;
        height: 6px;
      }
      path {
        fill: #fff;
      }
    }



  }


}