@import "../../../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";


.table_widget_modal {
  position: absolute;
  top: 17%;
  left: 50%;
  transform: translate(-50%, -20%);
  display: flex;
  flex-direction: column;
  align-items: center;
  max-height: 90vh;
  background-color: var(--background-color);
  border-radius: 18px;
  width: 90%;
  overflow-x: hidden;
  overflow-y: hidden;

  .title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 15px;
    width: 100%;
    border-radius: 18px 18px 0 0;
    background-color: var(--color-button);
    p {
      font-family: $mts-wide-b;
      font-size: 16px;
      color: #fff;
    }

    .svg_icon {
      path {
        fill: #fff;
      }
    }
  }

  ul {
    list-style: none;
    width: 100%;
    li {
      padding: 15px;
      width: 100%;
      border-bottom: var(--border-table-rows);
      font-family: $mts-text-r;
      font-size: 14px;
    }
  }

  .menu_container {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 10px;
    .button {
      display: flex;
      flex-direction: row;
      gap: 10px;
      padding: 5px;
      border-radius: 8px;
      background-color: var(--background-color-secondary);
      box-shadow: $shadow-middle;
      p {
        font-family: $mts-text-r;
        font-size: 14px;
      }
      .svg_icon {
        width: 20px;
        height: 20px;
        path {
          fill: var(--text-color);
        }
      }

    }
  }
}