@import "src/style/global.scss";
@import "src/style/screen-size.scss";

.confirmWindow {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  background-color: var(--background-color-secondary);
  box-shadow: $shadow-middle;
  border-radius: 24px;
  width: 340px;
  height: fit-content;
  padding: 28px 30px;
  display: flex;
  flex-direction: column;
  gap: 20px;

  @include respond-to(500px) {
    padding: 28px 20px;
    width: fit-content;
  }

  @include respond-to(xs) {
    font-size: 18px;
  }

  h1 {
    color: var(--text-color);
    font-family: $mts-wide-b;
    font-size: 20px;

    @include respond-to(710px) {
      font-size: 19px;
    }

    @include respond-to(xs) {
      font-size: 18px;
    }
  }

  &_btnsContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    &_btn {
      width: 90px;
      height: 63px;

      @include respond-to(500px) {
        // width: fit-content;
        height: fit-content;
      }
    }
  }
}
