@import "../../../../style/global.scss";
@import "src/style/screen-size.scss";
@import "src/App.scss";


.filter_item {
  display: flex;
  flex-direction: row;
  gap: 10px;
  padding: 7px;
  background-color: var(--background-color-secondary);
  border-radius: 8px;
  box-shadow: $filters2-shadow;
  width: 100%;

  p {
    font-family: $mts-text-r;
    font-size: 16px;
    color: var(--text-color);
  }

  input {
    background-color: var(--background-color-secondary);
    outline: none;
    border-radius: 8px;
    border: 1px solid var(--text-color);
    padding: 1px;
    color: var(--text-color);
    font-family: $mts-text-r;
    font-size: 16px;
    width: 100%;
  }
}